html, body {
	height: 100%;
	width: 100%;
	padding: 0;
	margin: 0;
	font-family: 'Helvetica Neue', arial, sans-serif;
	font-weight: 400;
	color: #444;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	/* source: http://lea.verou.me/css3patterns/#tablecloth */
	background-color:#ffffff;
	background-image: linear-gradient(90deg, rgba(110,200,188,.5) 50%, transparent 50%),
	linear-gradient(rgba(110,200,188,.5) 50%, transparent 50%);
	background-size:50px 50px;
}

* {
	box-sizing: border-box;
}

#app {
	display: flex;
	flex-direction: column;

	min-height: 20rem;
	max-width: 80rem;
	margin: 0 auto;
}

@media (min-width: 723px) {
	#app {
		flex-direction: row;
	}
}



